import orderList from 'module/core.order/src/components/page/core-order-list';
import orderDetail from 'module/core.order/src/components/page/core-order-detail';
import 'module/core.order/src/components';

export default {
    id: 'core.order',
    name: 'Bestellbersicht',
    description: 'Enter description here...',
    version: '1.0.0',
    targetVersion: '1.0.0',
    color: '#CA8EE0',
    icon: 'cart',

    routes: {
        index: {
            components: {
                default: orderList
            },
            path: 'index'
        },
        detail: {
            component: orderDetail,
            path: 'detail/:uuid',
            meta: {
                parentPath: 'core.order.index'
            }
        }
    },

    navigation: {
        root: [{
            'core.order.index': {
                icon: 'cart',
                color: '#CA8EE0',
                name: 'Bestellbersicht'
            }
        }]
    },

    commands: [{
        title: 'bersicht',
        route: 'order.index'
    }, {
        title: '%0 ffnen',
        route: 'order.detail'
    }],

    shortcuts: {
        index: {
            mac: {
                title: 'order.index.shortcut.mac',
                combination: [
                    'CMD',
                    'O'
                ]
            },
            win: {
                title: 'order.index.shortcut.win',
                combination: [
                    'CTRL',
                    'O'
                ]
            }
        }
    }
};



